* {
  margin: 0;
  padding: 0;
}

*, *::before, *::after {
  /* box-sizing: border-box; */
}


.card-review {
  height: 490px !important;
  width: 664px !important;
}

.p {
  padding: 30px 50px;
}
.bg {
  background-color: #edf6ff;
  width: 100%;
  height: 425px;
}

.img {
  display: none;
  margin-top: 23em !important;
  position: absolute;
}

.bg-clor {
  margin-top: 32em !important;
}

.img-logo {
  width: 100px;
  margin-left: 15em !important;
}

tr,
td,
p {
  padding: 3px;
}

.c-hr {
  position: relative;
  bottom: -40px;
  width: 250px;
  margin-left: 100px;
}

.btn {
  border-radius: 20px;
  background-color: #f47522;
  padding: 8px 19px;
  margin: 0;
  border: 1px;
}

#message {
  font-style: italic;
}

.footer {
  position: relative;
  bottom: -40px;
}

@media only screen and (max-width: 800px) {
  .img {
    display: block;
    margin-top: 23em;
    margin-right: 10em;
    position: absolute;
  }

  tr,
  td,
  p {
    padding: 10px;
  }
}

@media only screen and (max-width: 768px) {
  .img {
    display: block;
    margin-top: 23em;
    position: absolute;
  }

  tr,
  td,
  p {
    padding: 10px;
  }
}